* {
  font: 67.5%;

  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: black;
}
.icon-big-white {
  color: aliceblue;
  margin: 0 0.5rem;
}

input {
  margin: 0.2rem;
  display: block;
  height: 40px;
  width: 93%;
  background: rgb(165, 177, 179);
  color: rgb(103, 66, 172);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  transition: all 0.1s;
  border: none;
  padding-left: 1rem;
  outline: inherit;
}
textarea {
  margin: 0.2rem;
  display: block;
  height: 5rem;
  width: 93%;
  background: rgb(165, 177, 179);
  color: rgb(103, 66, 172);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  transition: all 0.1s;
  border: none;
  padding-left: 1rem;
  outline: inherit;
}

input:focus {
  font-size: 1.1rem;
  font-weight: 500;
}
label {
  color: rgb(38, 25, 116);
  margin-left: 10px;
}
.card-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20rem;
  border: 3px solid grey;
  padding: 2rem;
}
.login-button {
  text-align: center;
  text-decoration: none;
  display: inline-block;
  height: 40px;
  width: 99%;
  background: rgb(42, 211, 132);
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  margin: 10px 0.2rem;
  font-size: 1.2rem;
  font-weight: 700;
  transition: all 0.3s;
}
.login-button:hover {
  transform: translateY(-3px);
}
.login-button:active {
  transform: translateY(0px);
}
