.page-selector {
  margin-left: 1rem;
  margin-right: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
.lead__details {
  margin-left: 3rem;
}
table.table {
  text-align: center;
  padding: 1rem;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid rgb(196, 193, 193);
  height: auto;
  width: 75vw;
}
th,
td {
  padding: 1rem 0.2rem;
  text-align: center;
  border: 1px solid rgb(196, 193, 193);
}
tr:nth-child(odd) {
  background: rgba(168, 193, 207, 0.2);
}
tr:hover {
  background-color: rgba(133, 134, 216, 0.1);
}
