body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#outer-container {
  height: 100vh;
  width: 100vw;
}
.row {
  max-width: 114rem;
  margin: 0 auto;
}
.row:not(:last-child) {
  margin-bottom: 1rem;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row [class^="col-"] {
  float: left;
}
.row [class^="col-"]:not(:last-child) {
  margin-right: 6rem;
}
.row .col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3);
}
.row .col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}
.navbar {
  text-align: left;
  background: steelblue;
  padding: 0.75rem;
}

.menu {
  position: relative;
  height: 100%;
  display: contents;

  width: auto;
  overflow: hidden;
}
.menu-items {
  width: 100%;
  overflow: hidden;
}
.bm-menu-wrap {
  top: 0;
  left: 0;
  background: rgb(34, 73, 105);
}
.heading-logo {
  background: steelblue;
  text-align: center;
  border: 0.1rem solid white;
  width: 96%;
  top: 0%;
  padding: 0.5rem 0.2rem;
  margin: 0%;
  color: white;
  font-weight: 600;
  font-size: 1.3rem;
}
.bm-cross {
  top: 0;
  background: #bdc3c7;
}
.main-heading {
  margin: 1rem;
  padding: 0 3rem;
  font-size: 2rem;
  font-weight: 500;
}
.btn-menu {
  background: inherit;
  color: white;
  border: none;
  padding: 0.1rem 1rem;
  font: inherit;
  cursor: pointer;
  outline: 1px;
  margin: 0rem;
}
.menu-main-item {
  padding: 0.3rem 0.4rem;
  color: white;
  background: rgba(10, 0, 0, 0.981);
  font-size: 1.25rem;
  font-weight: 500;
}
/* For Firefox */
input[type="number"] {
  -moz-appearance: textfield;
} /* Webkit browsers like Safari and Chrome */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.menu-item {
  text-decoration: none;
  box-sizing: border-box;
  padding: 0.4rem 2rem;

  color: aliceblue;
  background: rgba(128, 128, 128, 0.2);
  display: list-item;
}
input.form-control {
  background: rgba(241, 236, 236, 0.863);
  border: 0.1rem solid grey;
}
textarea.form-control {
  background: rgba(241, 236, 236, 0.863);
  border: 0.1rem solid grey;
}
.form__details {
  margin: 2rem;
  padding: 2rem;
}
a {
  text-decoration: none;
}
.sort-icon {
  transition: all 0.1s;
  cursor: pointer;
}
.sort-icon:active {
  transform: scale(1.1);
}
td.btn-details {
  text-align: center;
  text-decoration: none;
  display: block;
  background: rgba(196, 151, 238, 0.065);
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0%;
  transition: all 0.2s;
}
.btn-details:hover {
  transform: translateY(-0.2rem);
}
.btn-details:active {
  transform: translateY(0px);
}

.btn-submit {
  text-align: center;
  text-decoration: none;
  display: inline-block;
  background: steelblue;
  color: inherit;
  border: none;
  border-radius: 10%;
  padding: 0.6rem 1rem;
  font: inherit;
  cursor: pointer;
  outline: 1px;
  margin: 1rem 0rem;
  color: white;
  transition: all 0.2s;
}
.btn-submit:hover {
  transform: translateY(-0.2rem);
}
.btn-submit:active {
  transform: translateY(0px);
}
.alert-danger {
  margin-top: 1rem;
  background: rgb(255, 0, 0, 0.75);
  width: 100;
  border: 0.1px solid black;
  color: white;
  text-align: center;
}
.btn-details-Link {
  display: flow-root;
  max-width: 100%;
  padding: 1rem;
}
.menu-logout {
  display: inline-block;
  text-decoration: none;
  position: absolute;
  right: 2rem;
  top: 1rem;
  color: whitesmoke;
  transition: all 0.2s;
}
.menu-logout:hover {
  transform: translateY(-0.1rem);
}
.menu-logout:active {
  transform: translateY(0);
}
